a:link {
  color: #000000;
  text-decoration: none;
}

a:visited {
  color: #000000;
  text-decoration: none;
}

a:hover {
  color: #993300;
  text-decoration: none;
}

body {
  background-color: #FFF;
  color: #333333;
  font-family: arial, geneva, sans-serif;
  font-size: 14px;
  margin: 0;
  word-wrap: break-word;
}

caption {
  color: #000000;
  font-size: 18px;
  font-weight: bold;
  margin-top: 5px;
  text-align: left;
}

.table {
  width: 100%;
}

form {
  margin: 0;
  padding: 0;
}

h1 {
  color: #000000;
  font-size: 22px;
  font-weight: bold;
  margin-bottom: 3px;
  margin-top: 3px;
}

h2 {
  color: #000000;
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 3px;
  margin-top: 3px;
}

h3 {
  color: #000000;
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 3px;
  margin-top: 3px;
}

h4 {
  color: #000000;
  font-size: 16px;
  font-weight: bold;
  margin-bottom: 3px;
  margin-top: 3px;
}

h5 {
  color: #000000;
  font-size: 15px;
  font-weight: bold;
  margin-bottom: 1px;
  margin-top: 1px;
}

h6 {
  color: #000000;
  font-size: 14px;
  font-weight: bold;
  margin-bottom: 1px;
  margin-top: 1px;
}

p {
  margin-top: 0;
}

table {
  background-color: #FFFFFF;
  border-bottom: 0 solid #CCCCCC;
  border-collapse: collapse;
  border-left: 0 solid #CCCCCC;
  margin: 3px 0 5px 0;
  padding: 0;
  width: 99%;
}

td {
  background-color: #FFF;
  border-bottom: 1px solid #CCCCCC;
  border-right: 1px solid #CCCCCC;
  padding: 5px;
  vertical-align: top;
}

td:last-child {
  background-color: #FFF;
  border-bottom: 1px solid #CCCCCC;
  border-right: 0 solid #CCCCCC;
  padding: 5px;
  vertical-align: top;
}

th {
  background-color: #E9E9E9;
  border-bottom: 1px solid #CCCCCC;
  border-right: 1px solid #CCCCCC;
  font-weight: bold;
  padding: 5px;
}

th:last-child {
  background-color: #E9E9E9;
  border-bottom: 1px solid #CCCCCC;
  border-right: 0 solid #CCCCCC;
  font-weight: bold;
  padding: 5px;
}

tr.highlight {
  background-color: #E9E9E9;
}

body#basket tr.highlight {
  background-color: transparent;
}

body#basket a {
  font-weight: bold;
}

body#basket table {
  border-collapse: collapse;
}

body#basket td,
body#basket th {
  background-color: transparent;
  border: 0;
  padding: 2px;
}

body#basket th {
  background-color: #EEE;
  border: 0;
}

body#basket th,
body#basket th[scope=col] {
  border: 0;
  text-align: center;
  vertical-align: middle;
}

body#basket th[scope=row] {
  border: 0;
  font-size: 89%;
  text-align: right;
  vertical-align: top;
  width: 10%;
}

#basket tr {
  border-bottom: 1px solid #DDD;
}

body#basket p {
  font-size: 85%;
  margin: 0.2em 0;
  text-indent: 0.5em;
}

.error {
  font-weight: bold;
}

.ex {
  font-family: "Courier New", Courier, monospace;
}

.inline {
  display: inline;
}

#bookcover {
  float: left;
  margin: 0;
  padding: 0;
}

#userresults {
  display: block;
  left: 1;
  position: absolute;
  word-wrap: break-word;
}

#userupdate input,
#userupdate textarea {
  border: 0;
  display: inline;
}

#action,
#addshelf,
#export,
#facetcontainer,
#members,
#navigation,
#opac-main-search,
#opaccredits,
#moresearches,
#remove-selected,
#selections-toolbar,
#toolbar,
.actions,
.breadcrumb,
.ft,
.koha_url,
.list-actions,
.navbar,
.noprint,
.pages,
.results_summary.actions,
.screen,
.suggestion,
.views,
a[href]::after,
fieldset.action,
h2 span.hint,
input,
td.resultscontrol,
.cart-control {
  display: none;
}

.row-fluid {
  margin-top: 30px;
}

#views,
#ulactioncontainer {
  display: none;
}

.main,
.table-bordered {
  border: 0;
}

@page {
  margin: 1cm;
}
#changelanguage {
  display: none;
}

#cartmenulink {
  display: none;
}

#listsmenu {
  display: none;
}

#koha_url {
  display: none;
}

.breadcrumb {
  display: none;
}

a[href]::after {
  display: none;
}

#social_networks {
  display: none;
}

a.tag_add {
  display: none;
}

.results_summary.online_resources {
  display: none;
}

a#Normalview,
a#MARCview,
a#ISBDview {
  display: none;
}

#addcomment {
  display: none;
}

#ulactioncontainer {
  display: none;
}

#usermenu {
  display: none;
}

#didyoumean {
  display: none;
}

#top-pages,
#bottom-pages {
  display: none;
}

#selections-toolbar {
  display: none;
}

.actions-menu {
  display: none;
}

#numresults a {
  display: none;
}

#numresults {
  float: left;
}